<template>
<div>
  <div class="right">
    <router-view></router-view>
  </div>
  <div class="left">
    <h1>listtree</h1>
    <a href="/" :class="{demoActive:(elSelect==='/demo1'||elSelect==='/')}"><h2>vue</h2>
      <div id="demo1" class="content">
        <p>vue</p>
        <h3>1. </h3>
        <span>   arrow  0</span>
        <h3>2. 18px</h3>
        <span>   indent  18 .lt-branch_level_1 span  margin-left  18px </span>
        <h3>3. </h3>
        <span>   .lt-branch span  lt-branch_level_1 span  lt-branch_level_0 span </span>
        <h3>4. </h3>
        <span>   .lt-branch_level_2 span:hover </span>
        <h3>5. </h3>
        <span>   open  ["always", 0, 1]</span>
        <h3>6. </h3>
        <span>   .lt-branch_active span  .lt-branch_level_2_active span </span>
        <h3>7. </h3>
        <span>   eventArea  "content" </span>
      </div>
    </a>
    <a href="demo2" :class="{demoActive:(elSelect==='/demo2')}"><h2>vue-element-admin</h2>
        <div id="demo2" class="content">
          <p>vue-element-admin</p>
          <h3>1. </h3>
          <span>  listDataiconFont AwesomeFont AwesomecsssasslessFont Awesomesassless</span>
          <h3>2. </h3>
          <span>   arrow Font Awesome arrowLeft  </span>
          <h3>3. </h3>
          <span>   .lt-branch </span>
          <h3>4. </h3>
          <span>   .lt-branch_level_2  background-color </span>
          <h3>5. </h3>
          <span>   .lt-branch:hover  .lt-branch_level_2:hover</span>
          <h3>6. </h3>
          <span>   animation 2</span>
          <h3>7. </h3>
          <span>   .lt-branch span  margin-left </span>
        </div>
      </a>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      elSelect: location.pathname
    }
  },
  created () {
    console.log(location.pathname)
  }
}
</script>
<style scoped>
@import url('./reset.css');
.left{
  position: fixed;
  padding:0 20px;
  width:540px;
  top:0;
  bottom:0;
  left:0;
  background:rgb(231, 230, 230);
  font-size: 14px;
  line-height: 22px;
  border-right:5px solid rgb(104, 102, 6);
  overflow-Y: auto;
}
.right{
  margin-left:580px;
  padding-top:12px;
}
.content{
  display:none;
  margin-left:12px;
  border:2px solid rgb(3, 67, 119);
  background:white;
  color:rgb(58, 58, 58);
  padding:8px;
  cursor: text;
}
.demoActive .content{
  display:block;
}
.content span{
  margin-left: 20px;
}
</style>



